.mainBlock {
    background-color: lightslategrey;
    height: 100vh;
}

.mainContainer {
    justify-content: space-between;
}

.mainBlockText {
    width: 300px;
    background-color: whitesmoke;
}

.mainBlockPhoto {
    width: 300px;
    height: 400px;
    background-color: maroon;
}

@media all and (max-width: 770px) {
    .mainContainer {
        justify-content: center;
    }

    .mainBlockText {
        text-align: center;
    }
}